.intro {
  align-items: center;
  background-color: #2e004b7e;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.intro h1,
.intro h2 {
  font-weight: normal;
}

.intro h1 {
  color: white;
  font-size: 1.2rem;
  margin: 0;
  margin-bottom: 2em;
}

.intro h2 {
  color: #00d363;
  font-size: 0.7rem;
  margin: 0;
  margin-bottom: 1.5em;
}

.intro form {
  border-radius: 5px;
  display: flex;
  overflow: hidden;
  width: 90%;
  max-width: 1000px;
}

.intro input,
.intro button {
  font-family: inherit;
  font-size: 0.9rem;
}

.intro input {
  flex-grow: 1;
  min-width: 0;
  padding: 1rem;
}

.intro button {
  background-color: #670080;
  border: none;
  color: white;
  flex-shrink: 0;
  padding: 1rem 2rem;
}

@media (min-width: 768px) {
  .intro h1 {
    font-size: 1.5rem;
  }
}

@media (min-width: 992px) {
  .intro h1 {
    font-size: 2rem;
  }

  .intro h2 {
    font-size: 1rem;
  }
}
