@import url(https://fonts.googleapis.com/css?family=Roboto:300,300i,400,500,700,900&display=swap);
.header {
  align-items: center;
  display: flex;
  font-size: 1rem;
  left: 0;
  margin: 0;
  padding: 10px 20px;
  position: absolute;
  right: 0;
  z-index: 1;
}

.nav__banner img {
  height: 1.5rem;
}

.nav__chat {
  display: none;
}

@media (min-width: 992px) {
  .nav__chat {
    display: block;
  }
}

.nav__login {
  all: unset;
  color: white;
  cursor: pointer;
  margin-right: 20px;
}

.nav-lg {
  flex-grow: 1;
  display: none;
  text-align: center;
}

.nav-lg__list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-lg__link {
  display: block;
  text-decoration: none;
}

.nav-lg__item--main-menu {
  position: relative;
  display: inline-block;
  margin: 0 20px;
}

.nav-lg__item--main-menu::before {
  position: absolute;
  content: "";
  height: 2px;
  width: 0;
  background-color: green;
  left: 50%;
  bottom: 0;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.nav-lg__item--main-menu:hover::before {
  width: 100%;
  left: 0;
}

.nav-lg__link--main-menu {
  color: white;
  font-weight: bold;
  height: 2em;
  line-height: 2em;
}

.nav-lg__link--dropdown::after {
  content: "";
  display: inline-block;
  margin-left: 0.5em;
  width: 0.5em;
  height: 0.5em;
  border: 0.1em solid white;
  border-top: none;
  border-left: none;
  -webkit-transform: translateY(-50%) rotate(45deg);
          transform: translateY(-50%) rotate(45deg);
}

.nav-lg__list--submenu {
  visibility: hidden;
  position: absolute;
  opacity: 0;
  left: 0;
  top: 100%;
  width: 10em;
  -webkit-transform: translateY(1em);
          transform: translateY(1em);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.nav-lg__item:hover .nav-lg__list--submenu {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

.nav-lg__item--submenu {
  display: block;
  margin: 0;
  text-align: start;
}

.nav-lg__link--submenu {
  padding-left: 1em;
  line-height: 2em;
  font-size: 0.8em;
  background-color: white;
  color: black;
}

.nav-lg__link--submenu:hover {
  background-color: #eee;
  color: black;
}

@media (min-width: 992px) {
  .nav-lg {
    display: block;
  }
}

.nav-sm {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
}

.nav-sm__list {
  background-color: white;
  left: 0;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  position: absolute;
  right: 0;
  top: 100%;
  width: 95%;
  visibility: hidden;
}

.nav-sm__list--visible {
  visibility: visible;
}

.nav-sm__sublist {
  list-style: none;
  margin: 0;
  padding: 0;
}

.nav-sm__link,
.nav-sm__sublink {
  background-color: white;
  color: #4a3600;
  display: block;
  font-size: 0.9rem;
  height: 0;
  line-height: 1.5em;
  text-decoration: none;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.nav-sm__link {
  padding-left: 20px;
  position: relative;
}

.nav-sm__link[data-symbol]::after {
  content: attr(data-symbol);
  font-size: 1.2em;
  position: absolute;
  right: 1em;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.nav-sm__sublink {
  padding-left: 40px;
}

.nav-sm__link:hover,
.nav-sm__sublink:hover {
  background-color: #eee;
}

.nav-sm__list--visible .nav-sm__link,
.nav-sm__list--visible .nav-sm__sublist--visible .nav-sm__sublink {
  height: 1.5em;
}

@media (min-width: 992px) {
  .nav-sm {
    display: none;
  }
}

.intro {
  align-items: center;
  background-color: #2e004b7e;
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
}

.intro h1,
.intro h2 {
  font-weight: normal;
}

.intro h1 {
  color: white;
  font-size: 1.2rem;
  margin: 0;
  margin-bottom: 2em;
}

.intro h2 {
  color: #00d363;
  font-size: 0.7rem;
  margin: 0;
  margin-bottom: 1.5em;
}

.intro form {
  border-radius: 5px;
  display: flex;
  overflow: hidden;
  width: 90%;
  max-width: 1000px;
}

.intro input,
.intro button {
  font-family: inherit;
  font-size: 0.9rem;
}

.intro input {
  flex-grow: 1;
  min-width: 0;
  padding: 1rem;
}

.intro button {
  background-color: #670080;
  border: none;
  color: white;
  flex-shrink: 0;
  padding: 1rem 2rem;
}

@media (min-width: 768px) {
  .intro h1 {
    font-size: 1.5rem;
  }
}

@media (min-width: 992px) {
  .intro h1 {
    font-size: 2rem;
  }

  .intro h2 {
    font-size: 1rem;
  }
}

.hosting {
  text-align: center;
}

.hosting__title h2 {
  color: #2e004b;
  font-size: 1.5rem;
  margin-top: 3em;
}

.hosting__title p {
  color: #919191;
}

.hosting__plans {
  list-style: none;
  margin: 0;
  padding: 0;
}

.hosting__plan {
  border: 1px solid #e0cce6;
  border-radius: 5px;
  margin: 10px 0;
  padding: 15px;
  text-align: left;
}

.hosting__plan .plan__logo {
  height: 3rem;
  width: 3rem;
}

.hosting__plan:nth-child(1) .plan__logo {
  background-color: red;
}

.hosting__plan:nth-child(2) .plan__logo {
  background-color: yellow;
}

.hosting__plan:nth-child(3) .plan__logo {
  background-color: green;
}

.hosting__plan:nth-child(4) .plan__logo {
  background-color: orange;
}

.hosting__plan .plan__title,
.hosting__plan .plan__price span {
  color: #2e004b;
  font-size: 1.5rem;
}

.hosting__plan .plan__description,
.hosting__plan .plan__price {
  color: #919191;
}

.hosting__plan .plan__title {
  margin-top: 1.5em;
}

@media (min-width: 768px) {
  .hosting__plans {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .hosting__plan {
    flex-basis: 49%;
  }
}

@media (min-width: 1200px) {
  .hosting__plan {
    flex-basis: 24%;
  }
}

.features {
  padding-bottom: 100px;
  padding-top: 100px;
}

.features__header {
  align-items: baseline;
  border-bottom: 1px solid #efe5f2;
  margin-bottom: 90px;
}

@media (min-width: 768px) {
  .features__header {
    display: flex;
  }
}

.features__title {
  color: #2e004b;
  flex-grow: 1;
  font-size: 1.5rem;
  margin-bottom: 8px;
}

.features__tab {
  display: inline-block;
}

.features__tab input {
  display: none;
}

.features__tab label {
  color: #919191;
  cursor: pointer;
  display: inline-block;
  font-size: 0.8rem;
  margin-left: 1em;
  padding: 0.5em 1em 2em;
  position: relative;
}

.features__tab input:checked + label {
  color: #2e004b;
}

.features__tab input:checked + label::after {
  background-color: #00d363;
  bottom: 0;
  content: "";
  position: absolute;
  height: 3px;
  left: 0;
  right: 0;
}

@media (min-width: 768px) {
  .features__tab label {
    font-size: 1rem;
  }
}

.tab-pane {
  display: none;
}

.tab-pane--active {
  display: block;
  margin-bottom: 30px;
}

.feature {
  margin-bottom: 30px;
}

.feature__icon {
  background-color: #eff7ff;
  border-radius: 50%;
  height: 82px;
  margin-bottom: 30px;
  width: 82px;
}

.feature:hover .feature__icon {
  background-color: #886cfe;
}

.feature__title {
  color: #1f1f1f;
  font-size: 1.25rem;
  margin-bottom: 0.5em;
}

.feature__text {
  color: #919191;
  line-height: 1.5em;
}

@media (min-width: 768px) {
  .feature {
    display: flex;
  }

  .feature__icon {
    flex-shrink: 0;
    margin-bottom: 0;
  }

  .feature__info {
    padding-left: 30px;
  }
}

@media (min-width: 1200px) {
  .tab-pane--active {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .feature {
    flex-basis: 48%;
  }
}

.footer {
  background-color: #2e004b;
}

.footer__top {
  border-bottom: 1px solid #919191;
  padding: 60px 0 30px;
}

.footer__copyright {
  padding: 30px 0;
}

.footer__copyright p {
  color: #919191;
  margin: 0;
  text-align: center;
}

.footer__copyright a {
  color: #00d363;
  text-decoration: none;
}

* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
  margin: 0;
  padding: 0;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}

